import { useMemo } from 'react';
import { type Address, type Chain } from 'viem';
import {
  useAccount,
  useBalance,
  useChains,
  useConfig,
  useConnect,
  useDisconnect,
  useSwitchChain,
} from 'wagmi';
import { useArbName } from './useArbName';
import { collapseString } from '@web3wallet/util';
import { useEns } from './useEns';

type UseWalletType = {
  /** The address of the wallet. */
  address?: Address;
  /** The resolved name of the wallet */
  resolvedName?: string;
  /** The ENS name of the wallet. */
  ensName?: string | null;
  /** The ENS avatar of the wallet. */
  ensAvatar?: string | null;
  /** The .arb name of the wallet. */
  arbName?: string | null;
  /** The chain id of the wallet. */
  chainId?: number;
  nativeBalance: bigint | null;
  nativeBalanceDecimals: number | null;
  nativeBalanceSymbol: string | null;
  /** Whether the wallet is connected. */
  isConnected: boolean;
  /** Disconnect the wallet. */
  disconnect: () => void;
  chain?: Chain;
  chains: ReturnType<typeof useChains>;
  switchChain: ReturnType<typeof useSwitchChain>['switchChain'];
  connect: ReturnType<typeof useConnect>['connect'];
  connectors: ReturnType<typeof useConnect>['connectors'];
  config: ReturnType<typeof useConfig>;
};

export function useWallet(): UseWalletType {
  const config = useConfig();
  const { address, isConnected, chainId, chain } = useAccount({ config });
  const { disconnect: disconnectWagmi } = useDisconnect({ config });
  const { connect, connectors, status, error, isError, isSuccess, data } = useConnect({ config });

  if (isError) {
    console.error('Error connecting to wallet', error);
  }
  console.log('connect', status);

  if (isSuccess) {
    console.log({
      status,
      address,
      isConnected,
      chainId,
      data,
    });
  }

  const chains = useChains({ config });
  const {
    switchChain,
    isError: switchChainIsError,
    error: switchChainError,
  } = useSwitchChain({ config });

  if (switchChainIsError) {
    console.error('Error switching chain', switchChainError);
  }

  // const { disconnect: disconnectMetamaskSdk, connected: connectedMetamaskSdk } =
  //   useDisconnectMetamaskSdk();

  const { data: balanceData } = useBalance({
    config,
    address,
    query: { enabled: isConnected },
  });

  const { ensName, ensAvatar } = useEns({
    config,
    address,
    enabled: isConnected,
  });

  const { arbName } = useArbName({ address });

  const disconnect = () => {
    disconnectWagmi();
    // if (connectedMetamaskSdk) {
    //   disconnectMetamaskSdk();
    // } else {
    //   disconnectWagmi();
    // }
  };

  const nativeBalanceData = useMemo(() => {
    return {
      nativeBalance: balanceData ? balanceData?.value : null,
      nativeBalanceDecimals: balanceData ? balanceData?.decimals : null,
      nativeBalanceSymbol: balanceData ? balanceData?.symbol : null,
    };
  }, [balanceData]);

  const resolvedName = useMemo(() => {
    const name = arbName ?? ensName ?? address;
    if (!name) return undefined;

    return collapseString(name, 6, 4);
  }, [ensName, arbName, address]);

  return {
    config,
    address,
    resolvedName,
    ensName,
    ensAvatar,
    arbName,
    isConnected,
    disconnect,
    connect,
    connectors,
    chainId,
    chain,
    chains,
    switchChain,
    ...nativeBalanceData,
  };
}
